import React, { useState } from "react";
import "./product.css";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import randomstring from "randomstring";

function AddProducts({ type }) {
  const [attribute, setAttribute] = useState(1);
  const [longDescription, setLongDescription] = useState(0);
  const [color, setColor] = useState("");
  const [skuNo] = useState(
    randomstring.generate(7) + "-UD-" + randomstring.generate(7)
  );
  const checkType = () => {
    switch (type) {
      case "mobiles":
        return true;
      case "laptops":
        return true;
      case "desktops & pc's":
        return true;
      case "food and grains":
        return false;
      case "fruits & vegetables":
        return false;
      default:
        return false;
    }
  };

  return (
    <div className="forms">
      <div className="form-group">
        <h2>Product Description</h2> <hr />
        <div className="form-group">
          <label htmlFor="exampleFormControlTextarea1">Product Title</label>
          <input
            name="title"
            type="text"
            className="form-control"
            placeholder="Product Title"
          />
        </div>
        <div className="form-group">
          <label htmlFor="exampleFormControlTextarea1">Short Description</label>
          <textarea
            type="text"
            name="shortDescription"
            className="form-control"
            placeholder="Give me a short description about product"
          />
        </div>
      </div>
      <div>
        <div className="d-flex align-items-center justify-content-between">
          <h3>Create attribute</h3>
          <div>
            <button
              type="button"
              className="btn border px-4 mx-4"
              onClick={() => setAttribute(attribute + 1)}
            >
              Add
            </button>
            <button
              type="button"
              className="btn border px-4 ml-4"
              onClick={() => setAttribute(attribute - 1)}
              disabled={attribute === 1}
            >
              Remove
            </button>
          </div>
        </div>
        {/* for multiple items and attributes  */}
        {Array(attribute)
          .fill("_")
          .map((_, i) => (
            <div key={i}>
              <div className="d-flex justify-content-between align-items-center mt-4">
                <h3 className="mt-4 mb-2">Form No .{i + 1}</h3>
                <div className="d-flex justify-content-between align-items-center mt-3">
                  SKU NO.
                  <input
                    className="ml-4"
                    style={{ border: "none" }}
                    type="text"
                    name="sku"
                    value={skuNo}
                  />
                </div>
              </div>
              <div className="input-group mb-3 p-1">
                <div className="input-group-prepend">
                  <span className="input-group-text" id="inputGroupFileAddon01">
                    Upload
                  </span>
                </div>
                <div className="custom-file">
                  <input
                    multiple
                    name="img"
                    type="file"
                    className="custom-file-input"
                    id="inputGroupFile01"
                    aria-describedby="inputGroupFileAddon01"
                    accept="image/png, image/jpeg,image/jpg,image/svg,image/webp"
                  />
                  <label
                    className="custom-file-label"
                    htmlFor="inputGroupFile01"
                  >
                    Choose file
                  </label>
                </div>
              </div>
              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span className="input-group-text">Price</span>
                  <input
                    name="price"
                    type="number"
                    className="form-control"
                    id="price"
                    placeholder="Rs."
                    min={0}
                    defaultValue={0}
                  />
                </div>
              </div>
              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span className="input-group-text">DiscountPrice</span>
                  <input
                    name="discountPrice"
                    type="number"
                    className="form-control"
                    id="discountPrice"
                    placeholder="Rs."
                    defaultValue={0}
                    min={0}
                  />
                </div>
              </div>
              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span className="input-group-text" id="inputGroupFileAddon01">
                    Quantity
                  </span>
                  <input
                    name="quantity"
                    type="number"
                    className="form-control"
                    placeholder="Select Quantity"
                    min={0}
                    defaultValue={0}
                  />
                </div>
              </div>
              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span className="input-group-text" id="inputGroupFileAddon01">
                    Weight
                  </span>
                  <input
                    name="weight"
                    type="number"
                    className="form-control"
                    placeholder="Select weight"
                    min={0}
                    defaultValue={0}
                  />
                </div>
              </div>
              {checkType() && (
                <div className="input-group mb-3">
                  <div className="input-group-prepend">
                    <span
                      className="input-group-text"
                      id="inputGroupFileAddon01"
                    >
                      Ram
                    </span>
                    <input
                      name="ram"
                      type="text"
                      className="form-control"
                      placeholder="Optional..."
                    />
                  </div>
                </div>
              )}
              {checkType() && (
                <div className="input-group mb-3">
                  <div className="input-group-prepend">
                    <span
                      className="input-group-text"
                      id="inputGroupFileAddon01"
                    >
                      Storage
                    </span>
                    <input
                      name="storage"
                      type="text"
                      className="form-control"
                      placeholder="Optional..."
                    />
                  </div>
                </div>
              )}
              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span className="input-group-text" id="inputGroupFileAddon01">
                    Size
                  </span>
                  <input
                    name="size"
                    type="text"
                    className="form-control"
                    placeholder="Optional..."
                  />
                  <div>
                    <p className="">* size is Optional</p>
                  </div>
                </div>
              </div>
              <div className="input-group mb-3">
                <div className="input-group-prepend">
                  <span className="input-group-text" id="inputGroupFileAddon01">
                    color
                  </span>
                  <div className="d-flex align-items-center">
                    <input
                      style={{ height: "35px" }}
                      type="color"
                      placeholder="Rightcolor"
                      value={color}
                      onChange={(e) => setColor(e.target.value)}
                    />
                    <input
                      style={{ height: "35px" }}
                      type="text"
                      name="color"
                      placeholder="Optional..."
                      onChange={(e) => setColor(e.target.value)}
                      value={color}
                    />
                  </div>
                  <p className="">* color is Optional</p>
                </div>
              </div>
            </div>
          ))}
      </div>
      <hr />
      {/* {used ckeditor 5 for product long description} */}
      <h4>LongDescription</h4>
      <CKEditor
        editor={ClassicEditor}
        data="<p>Write a LongDscription</p>"
        onChange={(event, editor) => {
          const data = editor.getData();
          console.log({ event, editor, data });
          setLongDescription(data);
        }}
      />
      <input
        value={longDescription}
        hidden
        name="longDescription"
        type="text"
        className="form-control"
        placeholder="tag"
      />
      <hr />
      <label>SEOTitle</label>
      <input
        name="seoTitle"
        type="text"
        className="form-control"
        placeholder="SEOTitle"
      />
      <label>SEODescription</label>
      <textarea
        name="seoDescription"
        type="text"
        className="form-control"
        placeholder="SEODescription"
      />
      <label>SEOAlt</label>
      <input
        name="seoAlt"
        type="text"
        className="form-control"
        placeholder="SEOAlt"
      />
      <label>Tag</label>
      <input
        name="tag"
        type="text"
        className="form-control"
        placeholder="tag"
      />
      <br />
    </div>
  );
}

export default AddProducts;
