import React, { useEffect, useState } from "react";
import "./product.css";
import { CCard, CCol, CButton } from "@coreui/react";
import { useSelector } from "react-redux";
import { selectUser } from "../../localyStore";

import axios from "axios";
import AddProducts from "./AddProducts";
import { useHistory } from "react-router";

const Categories = () => {
  const history = useHistory();
  const [data, setData] = useState(null);
  const [subCategories, setSubCategories] = useState(null);
  const [subCategoriesSecond, setSubCategoriesSecond] = useState(null);
  const [mainCategoryId, setMainCategoryId] = useState(null);
  const [categoryId, setCategoryId] = useState("");
  const [subCategoryId, setSubCategoryId] = useState("");
  const { token, role, _id } = useSelector(selectUser);

  const server = process.env.REACT_APP_SERVER;

  useEffect(() => {
    axios
      .get(server + process.env.REACT_APP_ADMIN_All_CATEGORY_GET, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((data) => {
        setData(data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [token]);

  // product create
  const ProductsPost = (e) => {
    let NewProduct;
    e.preventDefault();
    const data = e.target;
    // vendor or admin add products
    if (role === "admin") {
      NewProduct = process.env.REACT_APP_ADMIN_PRODUCT_POST;
    } else if (role === "vendor") {
      NewProduct = process.env.REACT_APP_ADMIN_PRODUCT_VENDOR_POST;
    }
    // attributes
    if (data.price.length > 0) {
      Array(data.price.length)
        .fill("_")
        .map((_, index) => {
          // append data
          let formData = new FormData();
          console.log(data.img);
          formData.append("name", data.title.value);
          formData.append("shortDescription", data.shortDescription.value);
          formData.append("longDescription", data.longDescription.value);
          formData.append("seoTitle", data.seoTitle.value);
          formData.append("seoDescription", data.seoDescription.value);
          formData.append("seoAlt", data.seoAlt.value);
          formData.append("mainCategory", mainCategoryId);
          formData.append("sub1Category", categoryId);
          formData.append("sub2Category", subCategoryId);
          formData.append("tag", data.tag.value);
          formData.append("vendor", _id);
          for (let j = 0; j < data.img[index].files.length; j++) {
            formData.append("productImage", data.img[index].files[j]);
          }
          formData.append("price", data.price[index].value);
          formData.append("discountPrice", data.discountPrice[index].value);
          formData.append("quantity", data.quantity[index].value);
          formData.append("weight", data.weight[index].value);
          if (data.ram?.[index]?.value) {
            formData.append("storage", data.ram?.[index]?.value);
          }
          if (data.storage?.[index]?.value) {
            formData.append("storage", data.storage?.[index]?.value);
          }
          if (data.size?.[index]?.value) {
            formData.append("size", data.size?.[index]?.value);
          }
          if (data.color?.[index]?.value) {
            formData.append("color", data.color?.[index]?.value);
          }
          formData.append("sku_number", data.sku[index].value);
          axios
            .post(server + NewProduct, formData, {
              headers: {
                Authorization: `bearer ${token}`,
                "Content-Type": "multipart/form-data",
              },
            })
            .then((res) => {
              setCategoryId(null);
              history.push("/productlist");
            });
        });
    } else {
      // append data
      let formData = new FormData();
      formData.append("name", data.title.value);
      formData.append("shortDescription", data.shortDescription.value);
      formData.append("longDescription", data.longDescription.value);
      formData.append("seoTitle", data.seoTitle.value);
      formData.append("seoDescription", data.seoDescription.value);
      formData.append("seoAlt", data.seoAlt.value);
      formData.append("mainCategory", mainCategoryId);
      formData.append("sub1Category", categoryId);
      formData.append("sub2Category", subCategoryId);
      formData.append("tag", data.tag.value);
      formData.append("vendor", _id);
      for (let j = 0; j < data.img.files.length; j++) {
        formData.append("productImage", data.img.files[j]);
      }
      formData.append("price", data.price.value);
      formData.append("discountPrice", data.discountPrice.value);
      formData.append("quantity", data.quantity.value);
      formData.append("weight", data.weight.value);
      if (data.ram?.value) {
        formData.append("storage", data.ram?.value);
      }
      if (data.storage?.value) {
        formData.append("storage", data.storage?.value);
      }
      if (data.size?.value) {
        formData.append("size", data.size?.value);
      }
      if (data.color?.value) {
        formData.append("color", data.color?.value);
      }
      formData.append("sku_number", data.sku.value);
      axios
        .post(server + NewProduct, formData, {
          headers: {
            Authorization: `bearer ${token}`,
            "Content-Type": "multipart/form-data",
          },
        })
        .then((res) => {
          setCategoryId(null);
          history.push("/productlist");
        });
    }
  };
  return (
    <CCard className="p-3">
      <div className="d-flex flex-row">
        <CCol sm="4" className=" d-md-block">
          <div className="form-group">
            <br />
            <label htmlFor="exampleFormControlSelect1">
              <h2>
                Select Category
                <hr />
              </h2>
            </label>
            <h2>Main Category</h2>
            <select
              defaultValue={[]}
              multiple
              onChange={(e) => {
                setSubCategories(e.target.value);
              }}
              className="form-control text-uppercase fontSize:50 p-4"
              id="exampleFormControlSelect1"
            >
              {/* <option value="" disabled selected hidden>
                Choose...
              </option> */}
              {data?.data.categories.map((i, index) => (
                <option onClick={() => setMainCategoryId(i._id)} key={index}>
                  {i.name}
                </option>
              ))}
            </select>
          </div>
          {subCategories && (
            <>
              <div className="form-group">
                <label htmlFor="exampleFormControlSelect2">
                  <h2>Sub Categories</h2>
                </label>
                <select
                  multiple
                  defaultValue={[]}
                  onChange={(e) => setSubCategoriesSecond(e.target.value)}
                  className="form-control text-uppercase fontSize:50 p-4"
                  id="exampleFormControlSelect2"
                >
                  {/* <option value="" disabled selected hidden>
                    Choose...
                  </option> */}
                  {data?.data.categories.map((i) =>
                    i.name === subCategories
                      ? i.children.map((i, index) => (
                          <option
                            onClick={() => setCategoryId(i._id)}
                            key={index}
                          >
                            {i.name}
                          </option>
                        ))
                      : ""
                  )}
                </select>
              </div>
              {subCategoriesSecond && (
                <div className="form-group">
                  <label htmlFor="sub">
                    <h2>Sub Categories</h2>
                  </label>
                  <select
                    multiple
                    defaultValue={[]}
                    className="form-control text-uppercase fontSize:50 p-4"
                    id="sub"
                  >
                    {data?.data.categories.map((i) =>
                      i.name === subCategories
                        ? i.children.map((i) =>
                            i.name === subCategoriesSecond
                              ? i.children.map((i, index) => (
                                  <option
                                    onClick={() => setSubCategoryId(i._id)}
                                    key={index}
                                  >
                                    {i.name}
                                  </option>
                                ))
                              : ""
                          )
                        : ""
                    )}
                  </select>
                </div>
              )}
            </>
          )}
        </CCol>
        <CCol sm="6">
          <br />
          <form onSubmit={ProductsPost} id="productForm">
            <AddProducts type={subCategoriesSecond} />
            <CButton
              disabled={
                !mainCategoryId || !subCategories || !subCategoriesSecond
              }
              type="submit"
              style={{ backgroundColor: "#03A89E", color: "white" }}
            >
              Save Product
            </CButton>
          </form>
        </CCol>
      </div>
    </CCard>
  );
};

export default Categories;
